body {
    background-image: linear-gradient(to right, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%);   margin-top: 100px;
 }
 
 .content {
    color: purple;
    height: 800px;
    text-align: center;
    margin-top: 50px;
    background-color: white;
    padding: 300px;
 }
 .buttons {
    display: flex;
    margin: 20px;
    padding: 20px;
    justify-content: space-around;
 }
    
 .list {
       background-color: white;
       border: none;
       color: purple;
       padding: 16px 32px;
       text-align: center;
       text-decoration: none;
       display: inline-block;
       font-size: 16px;
       margin: 4px 2px;
       transition-duration: 0.4s;
       cursor: pointer;
 }
 
 
 .button:hover {
    background-color: purple;
    color: white;
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
  }